<template>
    <div class="vis">
        <div class="vis-header">
            <h3>{{title}}</h3>
            <div class="slides is-centered"></div>
            <p v-html="slideText"></p>
        </div>
        <div class="vis-legend">
        </div>
        <div class="vis-content">
            <svg></svg>
        </div>
        <div class="vis-footer">
            <p>
                {{source}}
            </p>
        </div>
    </div>
</template>

<script>
    /* eslint-disable no-unused-vars,no-console */

    import * as d3 from 'd3';

    export default {
        name: 'Visualization',
        props: {
            containerWidth: Number,
            language: String
        },
        data: function () {
            return {
                languageSelected: this.language.toLowerCase(),
                title: this.language.toLowerCase() === "esp" ? "El ecosistema Fintech de América Latina y el Caribe" : "Latin America and the Caribbean Fintech Ecosystem",
                source:   this.language.toLowerCase() === "esp" ? "Datos: BID y Finnovista (2018)" : "Data: IDB & Finnovista (2018)",
                height: this.containerWidth > 600 ? this.containerWidth * 1 : this.containerWidth * 1.6,
                width: this.containerWidth,
                isMobile: this.containerWidth < 600,
                slideText: "",
                currentSlide: 0,
                disableButtons: false,
                fintechs: [],
                slides: [],
                // D3 variables
                svg: null,
                tooltip: null,
                simulation: null,
                center: {x: this.containerWidth / 2, y: this.containerWidth * 0.7 / 2},
                forceStrength: 0.1,
                fillColor1: "#00526E",
                fillColor2: d3.scaleOrdinal().domain([true, false]).range(['#00526E', '#e3120b']),
                fillColor3: d3.scaleOrdinal().domain([true, false]).range(['#00526E', '#e3120b']),
                fillColor4: [
                    "#5F4690",
                    "#1D6996",
                    "#38A6A5",
                    "#0F8554",
                    "#73AF48",
                    "#EDAD08",
                    "#E17C05",
                    "#CC503E",
                    "#94346E",
                    "#6F4070",
                    "#994E95",
                    "#666666"],
                fillColor5: [
                    '#52ad92',
                    '#91b07f',
                    '#d0b36c',
                    '#eca05d',
                    '#e57754',
                    '#de4d4b'].reverse(),
                bubbles: null,
                gLabels: null,
                labelsSlide4ESP: [
                    "Banca digital (26)",
                    "Financiamiento colectivo (89)",
                    "Gestión de finanzas empresariales (181)",
                    "Gestión de finanzas personales (90)",
                    "Gestión de patrimonio (55)",
                    "Negociación de activos financieros y mercados de capitales (61)",
                    "Pagos y remesas (285)",
                    "Préstamos (208)",
                    "Puntaje crediticio, identidad y fraude (47)",
                    "Seguros (53)",
                    "Tecnología empresarial para instituciones financieras (71)"],
                labelsSlide4ENG: [
                    "Digital Banks (26)",
                    "Crowdfunding (89)",
                    "Enterprise Financial Management (181)",
                    "Personal Financial Management (90)",
                    "Wealth Management (55)",
                    "Trading & Capital Markets (61)",
                    "Payments & Remittances (285)",
                    "Lending (208)",
                    "Scoring, Identity & Fraud (47)",
                    "Insurance (53)",
                    "Enterprise Technologies for Financial Institutions (71)"],
                labelsSlide5ESP: [
                    "Concepto/Idea",
                    "Demo/Prototipo",
                    "Versión beta",
                    "Lanzado",
                    "Listo para escalar",
                    "Crecimiento y expansión"],
                labelsSlide5ENG: [
                    "Concept/Idea",
                    "Demo/Prototype",
                    "Beta version",
                    "Launched",
                    "Ready to scale",
                    "Growth & expansion"]
            }
        },
        mounted: async function () {
            console.log("Component mounted: Visualization");
            console.log(`Vis attr: width ${this.containerWidth}px`);

            await this.loadData();
            this.loadUI();
            this.initSimulation();
        },
        updated: function () {
            console.log("Visualization updated");
        },
        methods: {
            loadData: function () {
                return new Promise(resolve => {
                    this.loadFintech().then(d => {
                        this.fintechs = d;
                        this.loadSlides().then(d => {
                            this.slides = d;
                            resolve();
                        });
                    });
                });
            },

            loadFintech: function () {
                return new Promise(resolve => {
                    let self = this;
                    d3.csv(process.env.BASE_URL + "data/fintech-list.csv", function (d) {
                        return {
                            id: +d.ID,
                            active: d.ACTIVE === "YES",
                            year: +d.YEAR,
                            country: self.languageSelected === "eng" ? d.COUNTRY_ENG : d.COUNTRY_ESP,
                            countryGroup: +d.COUNTRY,
                            sector: self.languageSelected === "eng" ? d.SECTOR_ENG : d.SECTOR_ESP,
                            sectorGroup: +d.SECTOR,
                            status: self.languageSelected === "eng" ? d.STATUS_ENG : d.STATUS_ESP,
                            statusGroup: +d.STATUS,
                            x: Math.random() * self.width,
                            y: Math.random() * self.height,
                            radius: self.width < 600 ? +d.STATUS * 0.3 + 1 : +d.STATUS * 0.5 + 1
                        }
                    }).then(function (data) {
                        console.log("Data Loaded: Fintech");
                        resolve(data);
                    });
                });
            },

            loadSlides: function () {
                return new Promise(resolve => {
                    let self = this;
                    d3.json(process.env.BASE_URL + `data/slide-texts-${self.languageSelected}.json`).then(function (data) {
                        resolve(data);
                    });
                });
            },

            loadUI: function () {
                let self = this;

                d3.select(".slides").selectAll("button")
                    .data(this.slides)
                    .enter()
                    .append("button")
                    .attr("class", function (d) {
                        return `button is-small slide-button-${d.index}`;
                    })
                    .html(function (d) {
                        return d.index;
                    })
                    .on("click", function (d) {
                        switch (d.index) {
                            case 1:
                                if (self.currentSlide !== 1 && !self.disableButtons) self.loadSlide1();
                                break;
                            case 2:
                                if (self.currentSlide !== 2 && !self.disableButtons) self.loadSlide2();
                                break;
                            case 3:
                                if (self.currentSlide !== 3 && !self.disableButtons) self.loadSlide3();
                                break;
                            case 4:
                                if (self.currentSlide !== 4 && !self.disableButtons) self.loadSlide4();
                                break;
                            case 5:
                                if (self.currentSlide !== 5 && !self.disableButtons) self.loadSlide5();
                                break;
                            case 6:
                                if (self.currentSlide !== 6 && !self.disableButtons) true; //self.loadSlide6();
                                break;
                            default:
                                break;
                        }
                    });

                this.tooltip = d3.select("body")
                    .append("div")
                    .attr("class", "aml-tooltip")
                    .text("orales");

                d3.select(".vis-content")
                    .append("img")
                    .attr("src", function(){
                        if(self.isMobile) {
                            return `images/cover-mobile-${self.languageSelected}.svg`;
                        } else {
                            return `images/cover-${self.languageSelected}.svg`;
                        }
                    })
                    .attr("class", "loading-image");
            },

            initSimulation: function () {

                let self = this;

                this.svg = d3.select("div.vis-content >svg")
                    .attr("width", this.width)
                    .attr("height", this.height);

                this.svg.style("display", "none");
                d3.select(".slides").style("display", "none");
                d3.select("img.loading-image")
                    .on("click", function () {
                        self.svg.style("display", "block");
                        d3.select(".slides").style("display", "block");
                        d3.select("img.loading-image").remove();
                        self.loadSlide1();
                    });

                this.bubbles = this.svg.selectAll('.bubble')
                    .data(this.fintechs, function (d) {
                        return d.id;
                    });

                let bubblesE = this.bubbles
                    .enter()
                    .append('circle')
                    .classed('bubble', true)
                    .attr('r', function (d) {
                        return d.radius;
                    })
                    .style('opacity', 0);

                this.bubbles = this.bubbles.merge(bubblesE);

                this.simulation = d3.forceSimulation()
                    .velocityDecay(0.4)
                    .force('collision', d3.forceCollide().radius(function (d) {
                        return d.radius
                    }))
                    .on('tick', this.ticked)
                    .stop()
                    .nodes(this.fintechs);

                this.gLabels = this.svg.append("g").attr("class", "g-labels");
            },

            charge: function (d) {
                return -this.forceStrength * Math.pow(d.radius + 1, 2);
            },

            ticked: function () {
                this.bubbles
                    .attr('cx', function (d) {
                        return d.x;
                    })
                    .attr('cy', function (d) {
                        return d.y;
                    });
            },

            //------------------------------------------------------------------------------------------

            loadSlide1: function () {
                console.log("Loading slide 1");
                let self = this;
                this.currentSlide = 1;
                this.slideText = this.slides[this.currentSlide - 1].text;

                this.bubbles
                    .attr("r", function (d) {
                        d.radius = self.isMobile ? +d.statusGroup * 0.8 + 1 : +d.statusGroup * 1.1 + 1;
                        return d.radius;
                    })
                    .style("fill", function (d) {
                        return self.fillColor1;
                    })
                    .classed("dead", false)
                    .classed("hidden", function (d) {
                        return d.year === 2018;
                    })
                    .transition()
                    .delay(function (d, i) {
                        return i;
                    })
                    .duration(500)
                    .style('opacity', 0.8);

                this.gLabels
                    .selectAll("*")
                    .remove();

                this.simulation
                    .velocityDecay(0.3)
                    .force('collision', d3.forceCollide().radius(function (d) {
                        return d.radius
                    }).iterations(2))
                    .force('charge', d3.forceManyBody().strength(this.chargeSlide1))
                    .force('x', d3.forceX().strength(this.forceStrength).x(function (d) {
                        return self.slide1Centers(d).x
                    }))
                    .force('y', d3.forceY().strength(this.forceStrength).y(function (d) {
                        return self.slide1Centers(d).y
                    }))
                    .alpha(1).alphaMin(0.5).restart();

                d3.selectAll(".button").classed("button-active", false);
                d3.select(`.slide-button-${this.currentSlide}`).classed("button-active", true);
            },

            chargeSlide1: function (d) {
                if (this.isMobile) {
                    return -this.forceStrength * Math.pow(d.radius + 0.5, 2);
                }
                return -this.forceStrength * Math.pow(d.radius + 2, 2);
            },

            slide1Centers: function (d) {
                let pos = {x: 0, y: this.height / 5 * 2};
                if (d.year === 2017) {
                    pos.x = this.width / 2;
                } else {
                    pos.x = this.width * 3;
                }
                return pos;
            },

            //------------------------------------------------------------------------------------------

            loadSlide2: function () {
                console.log("Loading slide 2");
                let self = this;
                this.currentSlide = 2;
                this.slideText = this.slides[this.currentSlide - 1].text;


                this.bubbles
                    .attr("r", function (d) {
                        d.radius = self.isMobile ? +d.statusGroup * 0.5 + 1 : +d.statusGroup * 0.8 + 1;
                        return d.radius;
                    })
                    .style("fill", function (d) {
                        if (d.year === 2018) {
                            return "#FA9500";
                        } else {
                            return self.fillColor2(d.active);
                        }
                    })
                    .classed("dead", function (d) {
                        return !d.active;
                    })
                    .classed("hidden", function (d) {
                        return d.year === 2018;
                    });

                this.gLabels.selectAll("*").remove();

                this.simulation
                    .velocityDecay(0.3)
                    .force('collision', d3.forceCollide().radius(function (d) {
                        return d.radius
                    }).iterations(2))
                    .force('charge', d3.forceManyBody().strength(this.chargeSlide2))
                    .force('x', d3.forceX().strength(this.forceStrength).x(function (d) {
                        return self.slide2Centers(d).x
                    }))
                    .force('y', d3.forceY().strength(this.forceStrength).y(function (d) {
                        return self.slide2Centers(d).y
                    }))
                    .alpha(1).alphaMin(0.5).restart();

                d3.selectAll(".button").classed("button-active", false);
                d3.select(`.slide-button-${this.currentSlide}`).classed("button-active", true);
            },

            chargeSlide2: function (d) {
                if (this.isMobile) {
                    return -this.forceStrength * Math.pow(d.radius + 1, 2);
                }
                return -this.forceStrength * Math.pow(d.radius + 2, 2);
            },

            slide2Centers: function (d) {
                let pos = {x: 0, y: this.height / 5 * 2};
                if (d.year === 2017 && d.active) {
                    pos.x = this.width / 3 * 2;
                } else if (d.year === 2017 && !d.active) {
                    pos.x = this.width / 3;
                } else {
                    pos.x = this.width * 2;
                }
                return pos;
            },

            //------------------------------------------------------------------------------------------

            loadSlide3: function () {
                console.log("Loading slide 3");
                let self = this;
                this.currentSlide = 3;
                this.slideText = this.slides[this.currentSlide - 1].text;

                this.bubbles
                    .attr("r", function (d) {
                        d.radius = self.isMobile ? +d.statusGroup * 0.5 + 1 : +d.statusGroup * 0.8 + 1;
                        return d.radius;
                    })
                    .style("fill", function (d) {
                        if (d.year === 2018) {
                            return "#FA9500";
                        } else {
                            return self.fillColor3(d.active);
                        }
                    })
                    .classed("dead", function (d) {
                        return !d.active;
                    })
                    .classed("hidden", false)
                    .classed("new", function (d) {
                        return d.year === 2018;
                    });

                this.gLabels.selectAll("*").remove();

                self.disableButtons = true;

                this.simulation
                    .velocityDecay(0.3)
                    .force('collision', d3.forceCollide().radius(function (d) {
                        return d.radius
                    }).iterations(2))
                    .force('charge', d3.forceManyBody().strength(this.chargeSlide3))
                    .force('x', d3.forceX().strength(this.forceStrength).x(function (d) {
                        return self.slide3CentersB(d).x
                    }))
                    .force('y', d3.forceY().strength(this.forceStrength).y(function (d) {
                        return self.slide3CentersB(d).y
                    }))
                    .alpha(1).alphaMin(0.5).restart();



                    setTimeout(function(){
                        self.simulation.force('x', d3.forceX().strength(self.forceStrength).x(function (d) {
                            return self.slide3Centers(d).x
                        }))
                            .force('y', d3.forceY().strength(self.forceStrength).y(function (d) {
                                return self.slide3Centers(d).y
                            }))
                            .alpha(1).alphaMin(0.5).restart();

                            self.disableButtons = false;
                    }, 1500);


                d3.selectAll(".button").classed("button-active", false);
                d3.select(`.slide-button-${this.currentSlide}`).classed("button-active", true);
            },

            chargeSlide3: function (d) {
                if (this.isMobile) {
                    return -this.forceStrength * Math.pow(d.radius + 1, 2);
                }
                return -this.forceStrength * Math.pow(d.radius + 2, 2);
            },

            slide3Centers: function (d) {
                let pos = {x: 0, y: this.height / 5 * 2};
                if (d.year === 2017 && d.active) {
                    pos.x = this.width / 2;
                } else if (d.year === 2017 && !d.active) {
                    pos.x = -this.width;
                } else {
                    pos.x = this.width / 2;
                }
                return pos;
            },

            slide3CentersB: function (d) {
                let pos = {x: 0, y: this.height / 5 * 2};
                if (d.year === 2017 && d.active) {
                    pos.x = this.width / 3;
                } else if (d.year === 2017 && !d.active) {
                    pos.x = -this.width;
                } else {
                    pos.x = this.width / 3 * 2;
                }
                return pos;
            },

            //------------------------------------------------------------------------------------------

            loadSlide4: function () {
                console.log("Loading slide 4");
                let self = this;
                this.currentSlide = 4;
                this.slideText = this.slides[this.currentSlide - 1].text;

                this.bubbles
                    .attr("r", function (d) {
                        d.radius = self.isMobile ? +d.statusGroup * 0.3 + 1 : +d.statusGroup * 0.6 + 1;
                        return d.radius;
                    })
                    .style("fill", function (d) {
                        if (d.active) {
                            return self.fillColor4[d.sectorGroup - 1];
                        } else {
                            return "#fff";
                        }
                    });

                this.gLabels.selectAll("*").remove();
                this.gLabels.selectAll("text")
                    .data(this.language === "esp" ? this.labelsSlide4ESP : this.labelsSlide4ENG)
                    .enter()
                    .append("text")
                    .style("opacity", 0)
                    .style("fill", function (d, i) {
                        return self.fillColor4[i];
                    })
                    .attr("class", "sector")
                    .attr("x", function (d) {
                        if (this.isMobile) {
                            return self.width * 0.05;
                        } else {
                            return self.width * 0.03;
                        }
                    })
                    .attr("y", function (d, i) {
                        if (self.isMobile) {
                            return (((i + 1) % 12)) * self.height / 12 - 15;
                        }
                        return (((i + 1) % 12)) * self.height / 12 - 20;
                    })
                    .html(function (d) {
                        return d;
                    })
                    .transition()
                    .delay(function (d, i) {
                        return 2000 + i * 100;
                    })
                    .duration(500)
                    .style("opacity", 1);

                this.simulation
                    .velocityDecay(0.3)
                    .force('collision', d3.forceCollide().radius(function (d) {
                        return d.radius;
                    }).iterations(2))
                    .force('x', d3.forceX().strength(this.forceStrength).x(self.width / 2))
                    .force('y', d3.forceY().strength(this.forceStrength).y(self.height / 2))
                    .alpha(1).restart()
                    .force('charge', d3.forceManyBody().strength(this.chargeSlide4))
                    .force('x', d3.forceX().strength(this.forceStrength).x(function (d) {
                        return self.slide4Centers(d).x
                    }))
                    .force('y', d3.forceY().strength(this.forceStrength).y(function (d) {
                        return self.slide4Centers(d).y
                    }))
                    .alpha(1)
                    .alphaMin(0.1)
                    .restart();

                d3.selectAll(".button").classed("button-active", false);
                d3.select(`.slide-button-${this.currentSlide}`).classed("button-active", true);
            },

            chargeSlide4: function (d) {
                if (this.isMobile) {
                    return -this.forceStrength * Math.pow(1, 2);
                }
                return -this.forceStrength * Math.pow(d.radius + 2, 2);
            },

            slide4Centers: function (d) {

                let pos = {x: 0, y: this.height / 2};
                if (!d.active) {
                    pos.x = -100;
                } else {
                    if (this.isMobile) {
                        pos.x = Math.random() * (this.width - (this.width * 0.1)) + this.width * 0.05;
                    } else {
                        pos.x = Math.random() * (this.width - (this.width * 0.06)) + this.width * 0.03;
                    }

                    pos.y = ((d.sectorGroup % 12)) * this.height / 12;
                }
                return pos;
            },

            //------------------------------------------------------------------------------------------

            loadSlide5: function () {
                console.log("Loading slide 5");
                let self = this;
                this.currentSlide = 5;
                this.slideText = this.slides[this.currentSlide - 1].text;

                this.bubbles
                    .attr("r", function (d) {
                        d.radius = self.isMobile ? +d.statusGroup * 0.3 + 1 : +d.statusGroup * 0.6 + 1;
                        return d.radius;
                    })
                    .style("fill", function (d) {
                        if (d.active) {
                            return self.fillColor5[d.statusGroup - 1];
                        }
                        return "#ffffff";
                    });

                this.gLabels.selectAll("*").remove();
                this.gLabels.append("line")
                    .attr("class", "status")
                    .attr("x1", 1 * this.width / 7)
                    .attr("y1", 30)
                    .attr("x2", 6 * this.width / 7)
                    .attr("y2", 30);

                for (let k = 1; k < 7; k++) {
                    this.gLabels.append("circle")
                        .attr("class", "status")
                        .attr("cx", k * this.width / 7)
                        .attr("cy", 30)
                        .attr("r", 10)
                        .style("fill", self.fillColor5[k - 1])
                        .on("mouseover", function () {
                            if (self.language === "esp") {
                                self.tooltip.html(self.labelsSlide5ESP[k - 1]);
                            } else {
                                self.tooltip.html(self.labelsSlide5ENG[k - 1]);
                            }

                            return self.tooltip.style("visibility", "visible");
                        })
                        .on("mousemove", function () {
                            return self.tooltip.style("top", (event.pageY - 10) + "px").style("left", (event.pageX + 10) + "px");
                        })
                        .on("mouseout", function () {
                            return self.tooltip.style("visibility", "hidden");
                        });

                }

                this.simulation
                    .velocityDecay(0.3)
                    .force('collision', d3.forceCollide().radius(function (d) {
                        return d.radius;
                    }).iterations(2))
                    .force('charge', d3.forceManyBody().strength(this.chargeSlide5))
                    .force('x', d3.forceX().strength(this.forceStrength).x(self.width / 2))
                    .force('y', d3.forceY().strength(this.forceStrength).y(self.height / 2))
                    .alpha(1).restart()
                    .force('x', d3.forceX().strength(this.forceStrength).x(function (d) {
                        return self.slide5Centers(d).x
                    }))
                    .force('y', d3.forceY().strength(this.forceStrength).y(function (d) {
                        return self.slide5Centers(d).y
                    }))
                    .alpha(1).alphaMin(0.5).restart();

                d3.selectAll(".button").classed("button-active", false);
                d3.select(`.slide-button-${this.currentSlide}`).classed("button-active", true);
            },

            chargeSlide5: function (d) {
                return -this.forceStrength * Math.pow(d.radius, 2);
            },

            slide5Centers: function (d) {
                let pos = {x: 0, y: this.height / 2};
                if (!d.active) {
                    pos.x = -100;
                } else {
                    pos.x = ((d.statusGroup % 7)) * this.width / 7;
                    pos.y = Math.random() * (this.height - (this.height * 0.3)) + this.height * 0.20;
                }
                return pos;
            },

            //------------------------------------------------------------------------------------------

            loadSlide6: function () {
                console.log("Loading slide 6");
                let self = this;
                this.currentSlide = 6;
                this.slideText = this.slides[this.currentSlide - 1].text;

                d3.selectAll(".button").classed("button-active", false);
                d3.select(`.slide-button-${this.currentSlide}`).classed("button-active", true);
            }
        }
    }
</script>

<style lang="scss">

    @import '~bulma/bulma';

    div.vis {
        padding-top: 10px;
        margin: 0 auto;
        background-color: #ffffff;
    }

    div.vis-header {
        h3 {
            margin-top: 10px;
        }

        p {
            font-family: 'Roboto', Helvetica, Arial, sans-serif;
        }

        @include mobile {
            padding: 0 10px;
        }
    }

    div.vis-footer {
        margin-top: 10px;
        padding: 5px 0;
        border-top: 1px solid grey;

        @include mobile {
            padding: 0 10px;
        }

        p {
            margin-bottom: 0;
        }
    }

    .footer-image {
        vertical-align: middle;
        padding: 0;
        margin: 0;
        height: 15px;
    }

    .slides {
        margin-bottom: 1em;
    }

    .button {
        margin-right: 1em;
    }

    .slide-button-6 {
        display: none;
        @extend .is-hidden;
    }

    .button-active {
        @extend .is-info;
    }

    circle.bubble {
        fill: #4a4a4a;
